<template>
  <div>
    <div class="portalSelect" @click="selectFn()">{{text}}</div>

    <van-action-sheet
      v-model="show"
      :actions="actions"
      :close-on-click-overlay="false"
      description="请选择项目区域"
      @select="onSelect"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      actions: [],
      text:'区域选择'
    };
  },
  created() {
    let portal = localStorage.getItem("portal");
    if (!portal) {
      this.getPortal();
    }
  },
  methods: {
    selectFn() {
      this.getPortal();
    },
    getPortal() {
      this.$http.areas.getPortal().then(res => {
        if (res.code == 200) {
          this.actions = []
          res.data.forEach(item => {
            this.actions.push(
              Object.freeze({
                ...item,
                name: item.areaName
              })
            );
          });
          this.show = true;
        }
      });
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      this.text = item.areaName;
      localStorage.setItem("portal", JSON.stringify(item));
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
.portalSelect {
  position: absolute;
  top: 0;
  right: 15px;
  width: 80px;
  text-align: right;
  line-height: 40px;
  color: #fff;
  font-size: 15px;
}
</style>
