<template>
  <div class="login">
    <PortalSelect></PortalSelect>
    <div class="logTop">
      <div class="top">
        <p class="logo">
          <img src="../../assets/imgs/login/logo.png" alt />
        </p>
        <p class="tit">安薪工程-项目端</p>
      </div>
    </div>
    <div class="form">
      <h2 class="title">项目登录</h2>
      <van-form @submit="onSubmit" validate-trigger="onChange" label-width="4em" label-align>
        <van-field
          v-model="username"
          left-icon="user-o"
          placeholder="请输入登录名"
          :rules="[{ required: true, message: '请填写登录名' }]"
        />
        <van-field
          v-model="password"
          :type="isPassword?'password':'text'"
          left-icon="icon iconfont iconpwss"
          right-icon="icon iconfont icontag"
          placeholder="请输入密码"
          :rules="[{ required: true, message: '请填写密码' }]"
          @click-right-icon="isPassword =! isPassword"
        />
        <div style="margin: 26px 16px;">
          <van-button round block type="info" native-type="submit">登录</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import PortalSelect from "../../components/Login/PortalSelect";
export default {
  name: "login",
  data() {
    return {
      show: false,
      actions: [],
      isPassword: true,
      username: "qy370844",
      password: "1234qwer"
    };
  },
  components: {
    PortalSelect
  },
  methods: {
    onSubmit() {
      let data = {
        loginName: this.username,
        password: this.password
      };
      this.$http.user.login(data).then(res => {
        if (res.code == 200) {
          localStorage.setItem("isLogin", true);
          localStorage.setItem("toKen", res.data.token);
          localStorage.setItem("userId", res.data.userid);
          this.$toast.success("登录成功");
          setTimeout(() => {
            this.$router.push("/");
          }, 1000);
        } else {
          this.$toast.fail("登录失败");
        }
      });
      //   localStorage.setItem("isLogin", true);
      //   this.$router.push("/");
    }
  },
  mounted() {},
  beforeDestroy() {}
};
</script>

