import Vue from "vue";
import VueRouter from "vue-router";

import Comming from "../views/comming/Comming";
import Login from "../views/sign/Login.vue";
import Home from "../views/home/Home.vue";
import Project from "../views/project/Project.vue";
import Service from "../views/service/Service.vue";
import Me from "../views/me/Me.vue";
//页面返回
VueRouter.prototype.goBack = function() {
  window.history.go(-1);
};

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    meta: {
      requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
      showTab: true, // 显示tab
    },
  },
  {
    path: "/project",
    name: "Project",
    component: Project,
    meta: {
      requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
      showTab: true, // 显示tab
    },
  },
  {
    path: "/service",
    name: "Service",
    component: Service,
    meta: {
      requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
      showTab: true, // 显示tab
    },
  },
  {
    path: "/me",
    name: "Me",
    component: Me,
    meta: {
      requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
      showTab: true, // 显示tab
    },
  },
  {
    path: "/comming", //登录
    name: "comming",
    component: Comming,
  },
  {
    path: "/sign/login", //登录
    name: "login",
    component: Login,
  },
];

const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    // 判断该路由是否需要登录权限
    if (localStorage.getItem("isLogin")) {
      next();
      // localStorage.setItem('comming', true)
    } else {
      // if (!localStorage.getItem("comming")) {
      //   next({
      //     path: "/comming",
      //   });
      // } else {
      //   next({
      //     path: "/sign/login", //登录页
      //   });
      // }
      next({
            path: "/sign/login", //登录页
      });
    }
  } else {
    next();
    // localStorage.setItem('comming', true)
  }
});

export default router;
