axios.create({
  timeout: 3*1000,
  withCredentials:true
})

let toast = ''

/**
 * 请求拦截
 */
axios.interceptors.request.use(config => {
  // vue.$toast('提示文案');
  toast = Toast.loading({
    duration:0,
    // message: '加载中...',
    forbidClick: true,
    loadingType: 'spinner'
  });

  let baseURL = '';
  if(config.url == '/login/portal'){
    baseURL = process.env.VUE_APP_BASE_API
  }else{
    let portal = localStorage.getItem('portal')
    if(portal){
      portal = JSON.parse(portal)
      baseURL = `http://${portal.ip}:${portal.port}/${portal.appName}`
      console.log(baseURL);
    }
  }
  config.baseURL = baseURL;
  if(config.method == 'post'){
    config.data = qs.stringify(config.data);
  }
//   if(localStorage.getItem('userId'))//搜索plus.uploader.createUpload，上传文件类接口，添加userId请求头+App.vue中login/valid接口
    // config.headers['userId'] = localStorage.getItem('userId')
//   config.headers['flag'] = '2'
//   removePending(config)
//   config.cancelToken = new CancelToken((c) => {
//     // 这里的axios标识我是用请求地址&请求方式拼接的字符串，当然你可以选择其他的一些方式
//     // pending.push({ u: config.url + JSON.stringify(config.data) + '&' + config.method, f: c })
//     pending.push({'UrlPath': config.url, 'Cancel': c})
//   })
  return config
}, error => {
  return Promise.reject(error)
})

/**
 * 响应拦截
 */
axios.interceptors.response.use(response => {
  // console.log('%c响应数据:', 'color:red');
  // 取消响应
//   removePending(response.config)
  toast.clear();
  return response.data
}, err => {
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
          console.log('错误请求')
        break;
      case 401:
          console.log('未授权，请重新登录')
        break;
      case 404:
        console.log('请求错误,未找到该资源')
        break;
      case 408:
        console.log('请求超时')
        break;
      case 500:
        console.log('服务器端出错')
        break;
      default:
        console.log(`连接错误${err.response.status}`)
    }
  } else {
    console.log('连接到服务器失败')
  }
  toast.clear();
  return Promise.reject(err)
})


export default axios
